<template>
    <div>
        <h1>Generador de rutinas</h1>
    </div>
    <opciones-ejercicios :ejercicios="ejerciciosArr"/>
</template>


<script>

import opcionesEjercicios from '../components/opcionesEjercicios.vue'
//Importamos el js

import getEjercicios from '../helper/getEjercicios'
console.log(getEjercicios())
export default {

    components : {
        opcionesEjercicios
    },
    data(){
        return{
            ejerciciosArr: [],
            ejercicio: null,
        }
    },
    methods:{
        async mixEjerciciosArray(){
            this.ejerciciosArr= await getEjercicios()
            const rndInt = Math.floor(Math.random()*4)

            this.ejercicio= this.ejerciciosArr[rndInt]
            console.log(this.ejerciciosArr)
        }
    },
    mounted(){
        this.mixEjerciciosArray()
    }
    
}
</script>


